.app-header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 20px 20px 20px;
  position: fixed;
  width: 100%;
  max-width: 100vw;
  z-index: 9;
}

.app-header a {
  grid-column-start: 2;
  justify-self: end;
}

.app-header img {
  height: 100%;
  height: 50px;
  transform: rotate(30deg);
}

.app-header img:hover {
  transform: scale(1.2);
}

@media only screen and (max-width: 600px) {
  .app-header h1 {
    font-size: 2rem;
    height: 3rem;
    width: 10rem;
  }
  .app-header img {
    display: none;
  }
}
.hamburger-lines {
  width: 50px;
  height: 50px;
  cursor: pointer;

  z-index: 99;
}

.hamburger-open {
  border: 1px solid white;
  border-radius: 1px;
  background-color: white;
  cursor: pointer;

  position: absolute;
  width: 50px;
  height: 4px;

  margin-top: 15px;

  transition: 0.4s ease-out;
  box-shadow: 2px 2px rgb(167, 165, 165);
}

.hamburger-close:nth-child(1) {
  background-color: whitesmoke;
  border: 1px solid whitesmoke;
  border-radius: 1px;
  cursor: pointer;
  height: 4px;
  width: 50px;
  transform: rotate(-49deg) translate(0, 30px);
  transition: 0.4s ease-in;

  margin-top: 15px;
  margin-left: -20px;
}

.hamburger-close:nth-child(3) {
  background-color: whitesmoke;
  border: 1px solid whitesmoke;
  border-radius: 1px;
  cursor: pointer;
  height: 4px;
  width: 50px;
  transform: rotate(45deg) translate(0, -30px);
  transition: 0.4s ease-in;

  margin-top: 15px;
  margin-left: -20px;
}
