.modal-background {
  background-color: var(--dark);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  height: 100%;
  width: 40%;
  top: 0;
  left: 0;
  position: fixed;
}

.modal-background h2 {
  color: white;
  font-family: "Exo", sans-serif;
  text-transform: uppercase;

  border: 1px solid var(--lightseagreen);
  width: 300px;

  text-align: center;
  font-weight: 400;
  font-size: 35px;
}

@media only screen and (max-width: 765px) {
  .modal-background h2 {
    font-size: 30px;
    height: 4rem;
    width: 12rem;
    padding: 10px 5px;
  }
}

@media only screen and (max-width: 600px) {
  .modal-background h2 {
    font-size: 20px;
    height: 3rem;
    width: 7rem;
    padding: 10px 5px;
  }
}

@media only screen and (max-width: 280px) {
  .modal-background h2 {
    font-size: 17px;
    height: 3rem;
    width: 6rem;
    padding: 10px 5px;
  }
}

.modal-background h2:hover{
  text-decoration: overline;
}

.modal-background a {
  text-decoration: none !important;
  font-size: 30px;

  margin-left: 10px;
  margin-top: 10px;
}

.menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: 100%;
  height: 100vh;
}